<template>
  <section class="homeview">
    <div class="inner">
      <h2 class="maintext">Ghibli Studio</h2>

        <Swiper
          :slidesPerView="4"
          :spaceBetween="30"
          :pagination="{
            clickable: true,
            }"
          :modules="modules"
          class="mySwiper"
          >

      <SwiperSlide class="movie" v-for="(item, index) in movieList" :key="index">
        <MovieList :propsdata="item"/>
      </SwiperSlide>
        </Swiper>

    </div>
      <button class="gotop" ref="gotop" @click="moveTop">Top</button>
  </section>
</template>

<script>

import {useStore} from 'vuex';
import {computed,onMounted,ref} from 'vue';
import {Autoplay, Pagination} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import MovieList from '../components/MovieList.vue';

export default {
  components: {
    MovieList,
    Swiper,
    SwiperSlide
  },
  setup (props,context) {
    const store = useStore();
    const movieList = computed( () => store.getters.getMovieList);
    const gotop = ref(null)
    
    // ref를 참조하려면 onMounted를 사용해야함
    // axios 실행 > vuex : mutation 실행
    onMounted( () => {
      // 내용물이 업데이트가 되었다면 App으로 intro가 사라지라고 hide 신호를 보낸다.
      let delay = setTimeout(()=>{
        clearTimeout(delay)
        context.emit('hide') 
      },1500)
    })

    const moveTop = () => {
    window.scrollTo({
        top : 0,
        left : 0,
        behavior: 'smooth'
      });
    }

    return{
      movieList,
      gotop,
      moveTop,
      modules : [Pagination, Autoplay]
    }
  }
}

</script>

<style scoped>

.maintext {
  text-align: center;
  font-size: 50px;
  font-weight: 800;
  margin-top: 30px;
  margin-bottom: 40px;  
}

.homeview{
  position: relative;
  display: block;
}

.movie{
  position: relative;
  display: block;
  width: 49%;
  background: #fff;
  margin-bottom: 70px;
  border-radius: 5px;
  color: #adaeb9;
  padding: 20px;
  box-shadow: 0px 13px 27px -5px rgba(50, 50, 93, 0.25),
            0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  cursor: pointer;
}

.gotop{
  position: fixed;
  right: 50px;
  bottom: 150px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  line-height: 50px;
  background-color: lightskyblue;
  color: #ddd;
  cursor: pointer;
  z-index: 9;
  border: 0;
  font-size: 18px;
  font-weight: 500;
  transition: background-color 0.3s;
}
.gotop:hover{
  color: #fff;
  background-color: rgb(88, 191, 255);
}
@media screen and (max-width: 1000px) {
  .movie{
    width: 95%;
  }
}
</style>