<template>
  <div 
    class="movie-box"
    :style="{
      backgroundImage: `url(${movieInfo.movie_banner})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center'
    }"
  >
    <a class="a-back" @click.stop="back">All List</a>

    <div class="movie-detail">

      <img class="movie-image" :src="movieInfo.image">
      <div class="movie-info-wrap">
        <h2 class="movie-title">Title : {{movieInfo.title}} <small>{{movieInfo.original_title}}</small></h2>
      <p class="movie-info">
        Release_Date : {{movieInfo.release_date}} <br>
        Director : {{movieInfo.director}}
        Producer : {{movieInfo.producer}}
        Running_Time : {{movieInfo.running_time}} 분
      </p>
      <p class="movie-desc">
        {{movieInfo.description}}
      </p>
      </div>

      
    </div>



  </div>

</template>

<script>
  import {computed} from 'vue';
  // router 를 통해서 전송받은 데이터 활용
  import {useRoute, useRouter} from 'vue-router'
  import {useStore} from 'vuex'
  export default {
    setup() {
      const route = useRoute();
      const id = route.params.id;
      // 상세정보 호출
      const store = useStore();
      store.dispatch('fetchMovieInfo', id)
      const movieInfo = computed(() => store.getters.getMovieInfo)
      const router = useRouter();
      const back = () => {
        router.push('/page-ghibli/');
      }
      return {
        id,
        movieInfo,
        back
      }
    }
  }
</script>

<style scoped>
.movie-box {
  position: relative;
  display: block;
  width: 100%;
  height: 100vh;
}

.a-back {
  position: relative;
  display: block;
  float: right;
  margin-right: 20px;
  margin-top: 10px;
  padding: 5px;
  background: skyblue;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
            0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 5px;
  font-size: 15px;
}

.movie-detail {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;

  width: 100%;
  height: 100%;
}

.movie-image {
  position: relative;
  display: block;
  width: 40%;
  height: 80%;
  border-radius: 10px;
  border: 10px solid #fff;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
            0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie-title {
  position: relative;
  display: block;
  width: 100%;
  font-size: 20px;
  font-weight: 800;
  background: #fff;
  border-radius: 5px;
  color: #000;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
            0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  margin-bottom: 20px;
}

.movie-title small{
  position: relative;
  display: block;
  float: right;
  color: #333;
  font-size: 12px;
  margin-top: 6px;
}
.movie-info {
  position: relative;
  display: block;
  width: 100%;
  font-size: 14px;
  font-weight: 400;
  background: #fff;
  border-radius: 5px;
  color: #000;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
            0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  margin-bottom: 20px;
}

.movie-info-wrap {
  position: relative;
  display: block;
  width: 45%;
}

.movie-desc {
  position: relative;
  display: block;
  width: 100%;
  font-size: 14px;
  font-weight: 400;
  background: #fff;
  border-radius: 5px;
  color: #000;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
            0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  margin-bottom: 20px;
}

@media screen and (max-width:1000px) {
  .movie-image {
    width: 95%;
    margin: 30px 0;
  }
  .movie-info-wrap {
    width: 95%;
    margin: 20px auto;
  }
}

</style>